
import { defineComponent } from "vue";
export default defineComponent({
  name: "UiButton",
  props: {
    type: {
      type: String
    }
  },
  computed: {
    mainClass(): string {
      const variants: Record<string, string> = {
        primary: "button button-primary",
        danger: "button button-danger"
      };
      return variants[this.$props.type || "primary"];
    }
  }
});
