
import { defineComponent } from "vue";
import { Button, TextField } from "@/components/ui";

export default defineComponent({
  props: {
    onsubmit: {
      type: Function,
      required: true
    }
  },
  components: {
    Button,
    TextField
  },
  data() {
    return {
      text: ""
    };
  },
  methods: {
    add() {
      if (this.$data.text === "") {
        return;
      }

      this.$props.onsubmit(this.$data.text);
      this.$data.text = "";
    }
  }
});
